import router, {MyRouter} from './router'

router.addRoutes(MyRouter)
// import { Message } from 'element-ui'
const whiteList = ['/login'] // 不重定向白名单
router.beforeEach((to, from, next) => {
// //  NProgress.start()
//   console.log('1===>to', to)
//   console.log('1===>from', from)
//   console.log('1===>next', next)
  if (sessionStorage.getItem('username')) {
    if (to.path === '/login') {
      next({path: '/'})
    } else {
      if (!localStorage.getItem('routers')) {
        localStorage.setItem('routers', JSON.stringify(MyRouter))
        router.addRoutes(MyRouter)
      }
      next()
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next('/login')
      // NProgress.done()
    }
  }
})

router.afterEach(() => {
  // NProgress.done() // 结束Progress
})



